.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media (max-width: 768px) {
  .workshops-column {
    padding: 0;
    margin-bottom: 1rem;
  }
}

.useful-links-title li {
  font-size: 24px;
  display: flex;
  justify-content: center;
}

.carousel-root {
  width: 64% !important;
  margin: auto !important;
  margin-top: 3% !important;
}

.carousel .slide {
  background: #fff !important;
  color: black;
  height: 100%;
}

.carousel .slide img {
  width: 139px !important;
  border-radius: 50%;
}

.myCarousel {
  background: #fafafa;
  margin-top: -6%;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  border: 1px solid #ddd;
  height: 286px;
  border-radius: 25px;
}

.carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  bottom: 5% !important;
}

.myCarousel h3 {
  color: #222;
  font-weight: 100;
  letter-spacing: 0.2px;
  margin-bottom: 4px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 17px;
}

.myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: #787878;
  font-size: 14px;
}

.myCarousel p {
  font-weight: 100 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 15px;
  font-family: sans-serif;
  max-height: 67px;
}

.myCarousel p:before {
  content: "“";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
}

.myCarousel p:after {
  content: "”";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.carousel .control-dots .dot {
  box-shadow: none !important;
  background: hsl(66, 71%, 45%) !important;
  outline: 0;
}

.carousel.carousel-slider .control-arrow {
  background: rgb(80, 136, 189) !important;
  border-radius: 25px;
  height: 50px !important;
  position: absolute;
  top: 35% !important;
}

.footer {
  text-align: center;
}

.services {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.desc {
  overflow-y: auto;
  /* Permite el desplazamiento vertical si es necesario */
}

.social-media-icons a {
  margin-right: 10px;

}

.footer {
  background-color: #f8f9fa;
  padding: 20px;
  margin-top: 50px;
}

@media only screen and (max-width: 768px) {
  .footer {
    margin-top: 0;
  }
}

.App {
  text-align: center;
}

@media only screen and (max-width: 934px) {
  .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none !important;
  }

  .myCarousel {
    background: #fafafa;
    margin-top: -9%;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 269px;
  }

  .carousel .slide img {
    width: 24% !important;
    border-radius: 50%;
  }

  .footer-right {
    text-align: right;
    margin-top: 5%;
    padding-right: 5%;
    width: 100%;
  }

  .footer-right h3 {
    color: white;
    font-weight: 600;
    font-size: 17px;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  .footer-right ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  #colorlib-logo,
  .location {
    font-family: 'Roboto Bold', sans-serif;
    font-weight: 700;
    /* 700 corresponde al peso de la fuente Roboto Bold */
  }

  .footer-right li {
    margin-bottom: 5px;
  }


  .footer-right a {
    color: white;
    text-decoration: none;
    font-size: 15px;
  }

  .footer-right a:hover {
    text-decoration: underline;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}