body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media (max-width: 768px) {
  .workshops-column {
    padding: 0;
    margin-bottom: 1rem;
  }
}

.useful-links-title li {
  font-size: 24px;
  display: flex;
  justify-content: center;
}

.carousel-root {
  width: 64% !important;
  margin: auto !important;
  margin-top: 3% !important;
}

.carousel .slide {
  background: #fff !important;
  color: black;
  height: 100%;
}

.carousel .slide img {
  width: 139px !important;
  border-radius: 50%;
}

.myCarousel {
  background: #fafafa;
  margin-top: -6%;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  border: 1px solid #ddd;
  height: 286px;
  border-radius: 25px;
}

.carousel .control-dots {
  padding-left: 5px !important;
  outline: 0;
  bottom: 5% !important;
}

.myCarousel h3 {
  color: #222;
  font-weight: 100;
  letter-spacing: 0.2px;
  margin-bottom: 4px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 17px;
}

.myCarousel h4 {
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: #787878;
  font-size: 14px;
}

.myCarousel p {
  font-weight: 100 !important;
  line-height: 29px !important;
  color: #222;
  font-size: 15px;
  font-family: sans-serif;
  max-height: 67px;
}

.myCarousel p:before {
  content: "\201C";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
}

.myCarousel p:after {
  content: "\201D";
  color: #aaa;
  font-size: 26px;
  font-family: monospace;
  font-weight: 100;
  line-height: 0;
}

.carousel .control-dots .dot {
  box-shadow: none !important;
  background: hsl(66, 71%, 45%) !important;
  outline: 0;
}

.carousel.carousel-slider .control-arrow {
  background: rgb(80, 136, 189) !important;
  border-radius: 25px;
  height: 50px !important;
  position: absolute;
  top: 35% !important;
}

.footer {
  text-align: center;
}

.services {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.desc {
  overflow-y: auto;
  /* Permite el desplazamiento vertical si es necesario */
}

.social-media-icons a {
  margin-right: 10px;

}

.footer {
  background-color: #f8f9fa;
  padding: 20px;
  margin-top: 50px;
}

@media only screen and (max-width: 768px) {
  .footer {
    margin-top: 0;
  }
}

.App {
  text-align: center;
}

@media only screen and (max-width: 934px) {
  .carousel-root {
    outline: 0;
    width: 93% !important;
    margin: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none !important;
  }

  .myCarousel {
    background: #fafafa;
    margin-top: -9%;
    width: 88%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    padding-bottom: 12.5%;
    padding-left: 5%;
    padding-right: 5%;
    border: 1px solid #ddd;
    height: 269px;
  }

  .carousel .slide img {
    width: 24% !important;
    border-radius: 50%;
  }

  .footer-right {
    text-align: right;
    margin-top: 5%;
    padding-right: 5%;
    width: 100%;
  }

  .footer-right h3 {
    color: white;
    font-weight: 600;
    font-size: 17px;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  .footer-right ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  #colorlib-logo,
  .location {
    font-family: 'Roboto Bold', sans-serif;
    font-weight: 700;
    /* 700 corresponde al peso de la fuente Roboto Bold */
  }

  .footer-right li {
    margin-bottom: 5px;
  }


  .footer-right a {
    color: white;
    text-decoration: none;
    font-size: 15px;
  }

  .footer-right a:hover {
    text-decoration: underline;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
/* sidebar.css */
#colorlib-aside {
    background: #f8f9fa;
    /* Un fondo claro */
    color: #333;
    /* Texto más oscuro para mayor legibilidad */
    padding: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    /* Sombra sutil */
}

#colorlib-aside .text-center {
    padding: 15px 0;
    /* Espaciado superior e inferior */
}

.author-img {
    margin-bottom: 20px;
    /* Espacio debajo de la imagen */
    border-radius: 50%;
    /* Hace la imagen circular si es cuadrada */
    overflow: hidden;
    /* Asegura que las esquinas se corten */
}

#colorlib-logo a {
    font-family: 'Roboto', sans-serif;
    color: #333;
    text-decoration: none;
    /* Sin subrayado */
    display: block;
    /* Bloque para margen inferior */
    margin-bottom: 20px;
    /* Espaciado debajo del título */
    font-size: 1.5em;
    /* Más grande */
}

.location {
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    color: #666;
    margin-bottom: 30px;
    /* Espaciado debajo de la ubicación */
    font-size: 1em;
}

#colorlib-main-menu .collapse {
    margin-top: 20px;
    /* Espacio arriba del menú */
}

#colorlib-main-menu ul {
    list-style: none;
    /* Elimina viñetas */
    padding: 0;
    margin: 0;
    /* Sin margen para alinear con el borde */
}

#colorlib-main-menu ul li {
    margin-bottom: 10px;
    /* Espacio entre elementos de lista */
}

#colorlib-main-menu ul li a {
    color: #333;
    text-decoration: none;
    /* Sin subrayado */
    transition: color 0.3s ease;
    /* Transición suave para el color */
}

#colorlib-main-menu ul li a:hover {
    color: #007bff;
    /* Color cuando el mouse pasa por encima */
}

#colorlib-main-menu ul li.active a {
    color: #007bff;
    /* Color para la sección actual */
    font-weight: bold;
    /* Más peso para la sección actual */
}

.navbar {
    font-family: 'Roboto', sans-serif;
    /* Asegúrate de que Roboto esté importado en tu proyecto */
    background-color: transparent;
    /* Fondo transparente para la barra de navegación */
    font-size: 1.1em;
}

.navbar .navbar-collapse {
    max-height: 340px;
    /* Altura máxima para permitir el desbordamiento de scroll si hay muchos elementos */
    overflow-y: auto;
    /* Permite el desplazamiento vertical */
}

#colorlib-main-menu ul {
    padding-left: 0;
    /* Alinea con el borde izquierdo */
}

#colorlib-main-menu ul li a {
    padding: 4px 0;
    /* Reduce el espaciado superior e inferior para cada enlace */
    display: block;
    /* Hace que el enlace llene el espacio del LI para un área de clic más grande */
    font-weight: 500;
    /* Peso de fuente para los enlaces */
    font-size: 1.5em;
    /* Tamaño de fuente estándar para mejor legibilidad */
}

#colorlib-main-menu ul li a:hover,
#colorlib-main-menu ul li a:focus {
    background-color: #f2f2f2;
    /* Fondo sutil al pasar el mouse o enfocar */
    border-radius: 5px;
    /* Bordes redondeados para el fondo */
}

.colorlib-footer {
    text-align: center;
    /* Centra el texto del footer */
    padding-top: 20px;
    /* Espaciado antes del contenido del footer */
    font-size: 0.85em;
    /* Tamaño de fuente más pequeño para el footer */
}

.colorlib-footer p {
    margin-bottom: 0;
    /* Elimina margen inferior para evitar espacios extra */
}

.colorlib-footer small {
    color: #aaa;
    /* Color más claro para el texto pequeño */
}

.social-media-icons {
    font-size: 3em;
    margin-top: 15px;
    /* Espacio encima de los iconos de redes sociales */
    display: flex;
    justify-content: center;
    /* Centra los iconos de redes sociales */
}

#colorlib-main-menu .fa-github,
#colorlib-main-menu .fa-linkedin {
    font-size: 3em !important;
    /* Aumenta la especificidad y fuerza la aplicación del estilo */
}

.social-media-icons a {
    margin: 0 10px;
    /* Espacio entre los iconos */
    color: #333;
    /* Color de los iconos */
}

.social-media-icons a:hover {
    color: #007bff;
    /* Cambia el color al pasar el mouse */
}

/* Estilos específicos para los íconos de redes sociales */
.fa-github {
    font-size: 2em;
    /* Tamaño de fuente para github */
}

.fa-linkedin {
    font-size: 2em;
    /* Tamaño de fuente para linkedin */
}
/* timeline.css */
.colorlib-narrow-content {
    max-width: 80%;
    margin: 0 auto;
    padding: 2em 0;
}

.heading-meta {
    color: #007bff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 0.9em;
    margin-bottom: 20px;
}

.colorlib-heading {
    margin-bottom: 30px;
    font-size: 2.5em;
    line-height: 1.4;
    font-weight: 700;
}

.timeline-centered {
    position: relative;
    margin-bottom: 3em;
}

.timeline-entry {
    position: relative;
    width: 100%;
    margin-bottom: 2em;
}

.timeline-entry-inner {
    position: relative;
    margin-left: 3em;
    padding: 20px;
    background: #f9f9f9;
    border-radius: 4px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
}

.timeline-icon {
    position: absolute;
    left: -2.5em;
    top: 0;
    background: #054282;
    color: #fff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    text-align: center;
    line-height: 50px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
}

.timeline-label h2 {
    font-weight: 700;
    margin-bottom: 15px;
    font-size: 1.5em;
    line-height: 1.2;
}

.timeline-label p {
    margin-bottom: 0;
    line-height: 1.6;
}

.timeline-label span {
    font-weight: 700;
    color: #010418;
}

/* Responsiveness */
@media (max-width: 768px) {
    .timeline-entry-inner {
        margin-left: 0;
    }

    .timeline-icon {
        left: 0;
        margin-bottom: -25px;
        margin-left: 0;
    }
}
.colorlib-narrow-content {
    margin: 0 auto;
    width: 80%;
}

.row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.row .col-md-12 {
    width: 100%;
}

.about-desc {
    font-weight: normal;
    /* Establece el texto normal por defecto para todo dentro de .desc */
    padding: 2em;
    border: 1px solid #e6e6e6;
    margin: 1em 0;
    background: #f9f9f9;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    color: #333;
    /* Color de texto predeterminado */
}

.about-desc strong {
    font-weight: bold;
    /* Solo el texto dentro de <strong> será en negrita */
    color: #242121;
    /* Opcional: Cambia el color para hacerlo destacar aún más :) */
}


.heading-meta {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 0.9em;
    color: #05406f;
    margin-bottom: 20px;
}

.services {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.services .icon {
    margin-bottom: 20px;
}


/* Estilos para igualar la altura de las cajas .desc */
.row .col-md-4 {
    display: flex;
    margin-bottom: 30px;
    /* Espacio entre las cajas */
}

.row .col-md-4 .services {
    flex-direction: column;
    display: flex;
    height: 100%;
}

.row .col-md-4 .services .desc {
    flex: 1 1;
}

/* Asegúrate de que todas las cajas .desc sean del mismo tamaño */
.row .col-md-4 .services .desc {
    display: flex;
    flex-direction: column;
}

/* Sección de habilidades */
.services.color-1,
.services.color-2,
.services.color-3,
.services.color-4,
.services.color-5,
.services.color-6 {
    border: 1px solid #e6e6e6;
    background: #f9f9f9;
}

/* Media queries para responsividad */
@media (max-width: 768px) {
    .colorlib-narrow-content {
        width: 95%;
    }

    .row .col-md-12,
    .row .col-md-4 {
        width: 100%;
    }
}
